import {
  Component, Input, Renderer, ElementRef, ViewEncapsulation, Optional, ComponentFactoryResolver,
  NgZone, ViewContainerRef, ViewChild, OnInit, AfterViewInit, OnDestroy, ChangeDetectorRef, ErrorHandler
} from '@angular/core';
import { NavControllerBase, App, Config, Platform, GestureController, DeepLinker, DomController, NavOptions } from 'ionic-angular';
import { TransitionController } from 'ionic-angular/transitions/transition-controller';
import { SuperTabs } from './super-tabs';


export class SuperTab extends NavControllerBase implements OnInit, AfterViewInit, OnDestroy {

  /**
   * Title of the tab
   */
  
  title: string;

  // TODO find less hacky approach
  // needed to make Ionic Framework think this is a tabs component... needed for Deeplinking
  get tabTitle() {
    return this.title;
  }

  // needed to make Ionic Framework think this is a tabs component... needed for Deeplinking
  get index() {
    return this.parent.getTabIndexById(this.tabId);
  }

  /**
   * Name of the ionicon to use
   */
  
  icon: string;

  /**
   * @input {Page} Set the root page for this tab.
   */
   root: any;

  /**
   * @input {object} Any nav-params to pass to the root page of this tab.
   */
   rootParams: any;

  
  tabId: string;

  get _tabId() {
    return this.tabId;
  }

  /**
   * Badge value
   * @type {Number}
   */
  
  badge: number;


  /**
   * Enable/disable swipe to go back for iOS
   * @return {boolean}
   */
  
  get swipeBackEnabled(): boolean {
    return this._sbEnabled;
  }
  set swipeBackEnabled(val: boolean) {
    this._sbEnabled = !!val;
    this._swipeBackCheck();
  }

  /**
   * @hidden
   */
  
  set _vp(val: ViewContainerRef) {
    this.setViewport(val);
  }

  /**
   * Indicates whether the tab has been loaded
   * @type {boolean}
   */
  private loaded: boolean = false;

  /**
   * A promise that resolves when the component has initialized
   */
  private init: Promise<any>;

  /**
   * Function to call to resolve the init promise
   */
  private initResolve: Function;

  constructor(
    parent: SuperTabs,
    app: App,
    config: Config,
    plt: Platform,
    el: ElementRef,
    zone: NgZone,
    rnd: Renderer,
    cfr: ComponentFactoryResolver,
    gestureCtrl: GestureController,
    transCtrl: TransitionController,
    errorHandler: ErrorHandler,
     private linker: DeepLinker,
    private _dom: DomController,
    private cd: ChangeDetectorRef
  ) {
    super(parent, app, config, plt, el, zone, rnd, cfr, gestureCtrl, transCtrl, linker, _dom, errorHandler);
    this.init = new Promise<void>(resolve => this.initResolve = resolve);
  }

  ngOnInit() {
    this.parent.addTab(this);
  }

  ngAfterViewInit() {
    this.initResolve();
  }

  ngOnDestroy() {
    this.destroy();
  }

  setActive(active: boolean) {
    if (active) {
      this.cd.reattach();
      this.cd.detectChanges();
    } else if (!active) {
      this.cd.detach();
    }
  }

  load(load: boolean) {
    if (load && !this.loaded) {
      this.init.then(() => {
        this.push(this.root, this.rootParams, { animate: false });
        this.loaded = true;
      });
    }
  }

  setBadge(value: number) {
    this.badge = value;
  }

  clearBadge() {
    delete this.badge;
  }

  increaseBadge(increaseBy: number = 1) {
    this.badge += increaseBy;
  }

  decreaseBadge(decreaseBy: number = 1) {
    this.badge = Math.max(0, this.badge - decreaseBy);
  }

  setWidth(width: number) {
    this.setElementStyle('width', width + 'px');
  }

  goToRoot(opts: NavOptions): Promise<any> {
    return this.setRoot(this.root, this.rootParams, opts);
  }

static decorators: DecoratorInvocation[] = [
{ type: Component, args: [{
  selector: 'super-tab',
  template: '<div #viewport></div><div class="nav-decor"></div>',
  encapsulation: ViewEncapsulation.None
}, ] },
];
/** @nocollapse */
static ctorParameters: () => ({type: any, decorators?: DecoratorInvocation[]}|null)[] = () => [
{type: SuperTabs, },
{type: App, },
{type: Config, },
{type: Platform, },
{type: ElementRef, },
{type: NgZone, },
{type: Renderer, },
{type: ComponentFactoryResolver, },
{type: GestureController, },
{type: TransitionController, },
{type: ErrorHandler, },
{type: DeepLinker, decorators: [{ type: Optional }, ]},
{type: DomController, },
{type: ChangeDetectorRef, },
];
static propDecorators: {[key: string]: DecoratorInvocation[]} = {
'title': [{ type: Input },],
'icon': [{ type: Input },],
'root': [{ type: Input },],
'rootParams': [{ type: Input },],
'tabId': [{ type: Input, args: ['id', ] },],
'badge': [{ type: Input },],
'swipeBackEnabled': [{ type: Input },],
'_vp': [{ type: ViewChild, args: ['viewport', {read: ViewContainerRef}, ] },],
};
}

interface DecoratorInvocation {
  type: Function;
  args?: any[];
}
